export default "import React, { useState, useEffect } from 'react'\nimport useEmblaCarousel from 'embla-carousel-react'\n\nexport function EmblaCarousel() {\n  const [emblaRef, emblaApi] = useEmblaCarousel({ loop: false })\n  const [scrollSnaps, setScrollSnaps] = useState([])\n  const [selectedSnap, setSelectedSnap] = useState(0)\n\n  const goTo = (index) => emblaApi?.goTo(index)\n  const setupSnaps = (emblaApi) => setScrollSnaps(emblaApi.snapList())\n  const setActiveSnap = (emblaApi) => setSelectedSnap(emblaApi.selectedSnap())\n\n  useEffect(() => {\n    if (!emblaApi) return\n\n    setupSnaps(emblaApi)\n    setActiveSnap(emblaApi)\n\n    emblaApi.on('reinit', setupSnaps)\n    emblaApi.on('reinit', setActiveSnap)\n    emblaApi.on('select', setActiveSnap)\n  }, [emblaApi])\n\n  return (\n    <div className=\"embla\">\n      <div className=\"embla__viewport\" ref={emblaRef}>\n        <div className=\"embla__container\">\n          <div className=\"embla__slide\">Slide 1</div>\n          <div className=\"embla__slide\">Slide 2</div>\n          <div className=\"embla__slide\">Slide 3</div>\n        </div>\n      </div>\n\n      <div className=\"embla__dots\">\n        {scrollSnaps.map((_, index) => (\n          <button\n            className={'embla__dot'.concat(\n              index === selectedSnap ? ' embla__dot--selected' : ''\n            )}\n            key={index}\n            onClick={() => goTo(index)}\n          >\n            {/* Button content */}\n          </button>\n        ))}\n      </div>\n    </div>\n  )\n}\n";